<template>
  <div class="teachingTeam padding students" v-loading.body="bodyLoading">
    <crumb style="padding: 0 5px;">
      <div
        class="new--btn"
        slot="right"
        @click="openAdd"
      >+ 新建</div>
    </crumb>
    <div class="teachers1-inp">
      <el-form class="demo-form-inline">
        <span class="ssssss">学年：</span>
        <el-select v-model="listQuery.grade" placeholder="请选择学年" class="pppp">
          <el-option label="请选择" value=""></el-option>
          <el-option v-for="item in yearOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
        <span class="sssss">学期：</span>
        <el-select v-model="listQuery.term" placeholder="请先选择学年" class="pppp">
          <el-option label="请选择" value=""></el-option>
          <el-option
            v-for="item in termOptions"
            :key="item.id"
            :label="item.label"
            :value="item.id"
          ></el-option>
        </el-select>
        <span class="sssss">课程搜索：</span>
        <el-input style="width: 200px" v-model="listQuery.keyword" placeholder="请输入课程"></el-input>
        <div class="search--btn" @click="getList" style="margin: 5px 0 20px 20px">搜索</div>
      </el-form>
    </div>

    <div class="teachers1-tab">
      <template>
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column label="课程编码" width="100" prop="code"></el-table-column>
          <el-table-column label="课程名称" prop="name" width="250"></el-table-column>
          <el-table-column label="学年" width="200">
            <template slot-scope="scope">
              <span>{{scope.row.grade}} - {{Number(scope.row.grade) + 1}}学年</span>
            </template>
          </el-table-column>
          <el-table-column label="课程负责人" prop="realName" width="150"></el-table-column>
          <el-table-column label="团队成员" prop="teacherNames"></el-table-column>
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.row)">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;&nbsp;编辑
              </el-button>
              <el-button
                style="color: #ff5858;"
                type="text"
                size="small"
                @click="handleDelete(scope.row)"
              >
                <SvgIcon icon-class="iconshanchu"></SvgIcon>&nbsp;&nbsp;删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <!-- 新增编辑 -->
    <el-dialog
      width="40%"
      :title="dialogTitle"
      :visible.sync="userDialogFormVisible"
      class="set-el-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="选择课程：" prop="courseId">
          <el-select v-model="form.courseId" placeholder="请选择课程" size="small" style="width: 100%">
            <el-option
              v-for="item in courseOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择学年：" prop="grade">
          <el-select v-model="form.grade" placeholder="请选择学年" size="small" style="width: 100%">
            <el-option v-for="item in yearOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择学期：" prop="term">
          <el-select v-model="form.term" placeholder="请选择学期：" size="small" style="width: 100%">
            <el-option v-for="item in termOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="课程负责人：">
          <div>{{userName}}</div>
        </el-form-item>
        <el-form-item label="选择教师：">
          <el-select
            v-model="teacher"
            filterable
            placeholder="请选择教师"
            size="small"
            style="width: 50%"
          >
            <el-option
              v-for="item in teacherOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
          <div class="teachers1_add" @click="addTeacher">+添加到团队</div>
        </el-form-item>
        <el-form-item label="已添加成员：" prop="teacherIds">
          <div class="teachers1_name" v-for="(item, index) in teamList" :key="index">
            {{item.name}}&nbsp;&nbsp;&nbsp;
            <i class="el-icon-circle-close" style="cursor: pointer" @click="deleteTeacher(index)"></i>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="userDialogFormVisible = false">取 消</el-button>
        <el-button size="small" type="primary" v-if="dialogTitle==='新建教学团队'" @click="submitAdd('form')">确 定</el-button>
        <el-button size="small" type="primary" v-else @click="submitEdit('form')">确 定</el-button>
      </div>
    </el-dialog>
    <div>
      <template>
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="listQuery.page"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="listQuery.rows"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userName: localStorage.getItem('userName'),
      listQuery: {
        grade: '',
        term: '',
        keyword: '',
        page: 1,
        rows: 15
      },
      rules: {
        courseId: [
          {
            required: true,
            message: "请选择课程",
            trigger: "blur"
          }
        ],
        grade: [
          {
            required: true,
            message: "请选择学年",
            trigger: "blur"
          }
        ],
        term: [
          {
            required: true,
            message: "请选择学期",
            trigger: "blur"
          }
        ],
        teacherIds: [
          {
            required: true,
            message: "请选择成员",
            trigger: "blur"
          }
        ]
      },
      teamList: [],
      userDialogFormVisible: false,
      yearOptions: [],
      termOptions: [
        {
          id: 1,
          label: '第一学期'
        },
        {
          id: 2,
          label: '第二学期'
        }
      ],
      tableData: [],
      dialogTitle: '新建教学团队',
      form: {
        courseId: '',
        grade: '',
        term: '',
        teacherIds: ''
      },
      teacher: '',
      courseOptions: [],
      teacherOptions: [],
      total: 0,
      bodyLoading: false
    }
  },
  mounted() {
    this.schedulingGrades()
    this.courseAll()
    this.teacherAll()
    this.getList()
  },
  methods: {
    // 重置查询条件
    resetQuery() {
      this.listQuery = {
        grade: '',
        term: '',
        keyword: '',
        page: 1,
        rows: 15
      }
    },
    // 点击新增
    openAdd() {
      this.userDialogFormVisible = true
      this.dialogTitle = '新建教学团队'
      this.form = {
        courseId: '',
        grade: '',
        term: '',
        teacherIds: ''
      }
      this.teacher = ''
      this.teamList = []
      this.$nextTick(() => {
        this.$refs.form.resetFields()
      })
    },
    // 点击编辑
    handleEdit(row) {
      this.userDialogFormVisible = true
      this.dialogTitle = '编辑教学团队'
      this.$nextTick(() => {
        this.form = JSON.parse(JSON.stringify(row))
        this.teacher = ''
        this.teamList = []
        if(this.form.teacherIds === '') {
          this.form.teacherIds = ''
        } else {
          this.form.teacherIds.split(',').forEach((item, i) => {
            this.teamList.push({
              id: item,
              name: row.teacherNames.split(',')[i]
            })
          })
        }
        this.$refs.form.resetFields()
      })
    },
    // 新增提交
    submitAdd(formName) {
      let arr = []
      if(this.teamList.length > 0) {
        this.teamList.forEach(item => {
          arr.push(item.id)
        })
        this.form.teacherIds = arr.join(',')
      } else {
        this.form.teacherIds = ''
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$post('/basic/scheduling/addTeacherTeam', this.form).then(res => {
            if(res.status === 1) {
              this.$notify.success({
                title: '提示',
                message: res.message,
                duration: 2000
              })
              this.resetQuery()
              this.getList()
              this.userDialogFormVisible = false
            } else {
              this.$notify.error({
                title: '提示',
                message: res.message,
                duration: 2000
              })
            }
          })
        }
      })
    },
    // 编辑提交
    submitEdit(formName) {
      let arr = []
      if(this.teamList.length > 0) {
        this.teamList.forEach(item => {
          arr.push(item.id)
        })
        this.form.teacherIds = arr.join(',')
      } else {
        this.form.teacherIds = ''
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let form = {
            courseId: this.form.courseId,
            grade: this.form.grade,
            term: this.form.term,
            teacherIds: this.form.teacherIds,
            schedulingIds: this.form.schedulIds
          }
          this.$put('/basic/scheduling/editTeacherTeam', form).then(res => {
            if(res.status === 1) {
              this.$notify.success({
                title: '提示',
                message: res.message,
                duration: 2000
              })
              this.resetQuery()
              this.getList()
              this.userDialogFormVisible = false
            } else {
              this.$notify.error({
                title: '提示',
                message: res.message,
                duration: 2000
              })
            }
          })
        }
      })
    },
    // 点击删除
    handleDelete(row) {
      this.$confirm('此操作将删除学校信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const data = {
          schedulingIds: row.schedulIds
        }
        this.$post('/basic/scheduling/deleteTeam', data).then(res => {
          if(res.status === 1) {
            this.$notify.success({
              title: '提示',
              message: res.message,
              duration: 2000
            })
            this.resetQuery()
            this.getList()
          } else {
            this.$notify.error({
              title: '提示',
              message: res.message,
              duration: 2000
            })
          }
        })
      }).catch(() => {
        this.$notify.error({
          title: '提示',
          message: '已取消删除!',
          duration: 2000
        })
      })
    },
    // 添加成员
    addTeacher() {
      if(this.teacher == '') {
        return
      }
      let teacher = this.teacherOptions.find(item => {
        return item.id === this.teacher
      })
      let chooseTeacher = this.teamList.find(item => {
        return item.id === teacher.id
      })
      if(chooseTeacher) {
        this.$notify({
          title: '提示',
          message: '该老师已被添加！',
          type: 'warning',
          duration: 1500
        })
      } else {
        this.teamList.push(teacher)
      }
    },
    // 删除成员
    deleteTeacher(index) {
      this.teamList.splice(index, 1)
    },
    // 切换一页的条数
    handleSizeChange(size) {
      this.listQuery.rows = size
      this.getList()
    },
    // 换页
    handleCurrentChange(page) {
      this.listQuery.page = page
      this.getList()
    },
    // 获取列表
    getList() {
      this.bodyLoading = true
      this.$post('/basic/scheduling/teamlist', this.listQuery).then(res => {
        if(res.status === 1) {
          this.tableData = res.data
          this.total = res.total
        } else {
          this.$notify.error({
            title: '提示',
            message: res.message,
            duration: 2000
          })
        }
        this.bodyLoading = false
      }).catch(err => {
        this.bodyLoading = false
      })
    },
    // 学年
    schedulingGrades() {
      this.$get("basic/version/grades").then(res => {
        if (res.status === 1) {
          this.yearOptions = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          })
        }
      })
    },
    //所有课程
    courseAll() {
      const url = '/basic/scheduling/courseByLeader'
      this.$get(url)
        .then(res => {
          if (res.status === 1) {
            this.courseOptions = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //所有教师
    teacherAll() {
      this.$get('/basic/teacher/realall').then(res => {
        if (res.status === 1) {
          this.teacherOptions = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
  }
};
</script>

<style lang="scss">
.teachingTeam {
  .teachers1-inp {
    width: 100%;
    overflow: hidden;
    background-color: #fff;
    padding: 13px 20px 0;
    margin-bottom: 20px;
    box-sizing: border-box;
    border-radius: 10px;

    .pppp {
      width: 15%;
      float: left;
      margin: 0;
      margin-top: 6px;
    }
  }

  .teachers1-tab {
    background-color: #fff;
    padding-bottom: 20px;
    padding-bottom: 0;
    border-radius: 10px 10px 0 0;
  }

  .teachers1_name {
    padding: 3px 10px;
    background: rgba(3, 177, 135, 0.1);
    border: 1px solid rgba(3, 177, 135, 1);
    border-radius: 5px;
    display: inline-block;
    text-align: center;
    line-height: 26px;
    color: rgba(3, 177, 135, 1);
    margin-right: 15px;
  }

  .teachers1_add {
    width: 120px;
    height: 32px;
    background: rgba(3, 177, 135, 1);
    border-radius: 5px;
    color: #fff;
    line-height: 32px;
    text-align: center;
    display: inline-block;
    cursor: pointer;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;
  }

  .ssssss {
    float: left;
    font-size: 14px;
    line-height: 45px;
    font-weight: bold;
    color: #696969;
    margin-top: 3px;
  }

  .sssss {
    margin-left: 16px;
    float: left;
    font-size: 14px;
    line-height: 45px;
    font-weight: bold;
    color: #696969;
    margin-top: 3px;
  }

  .el-form--inline .el-form-item {
    margin-left: 20px;
  }

  .span-dv {
    cursor: pointer;
    color: #000;
    padding: 0 8px;
  }

  .teachers1-btn {
    margin-top: 18px;
  }

  .teachers1-btn1 {
    margin-top: 18px;
  }

  .block {
    float: left;
    width: 100%;
    text-align: right;
    background-color: #fff;
    padding: 15px;
    box-sizing: border-box;
    border-radius: 0 0 10px 10px;
  }

  .steps-dv1 {
    position: relative;

    .steps-btn {
      position: absolute;
      top: 66px;
      left: 54px;
    }
  }
}
</style>



